import React, { FunctionComponent } from "react";
import Autocomplete from "@material-ui/lab/Autocomplete";
import TextField from "@material-ui/core/TextField";

import VisibilityTransition from "./VisibilityTransition";
import Response from "./Response";
import useFocus from "./useFocus";

import { IQuestionExtendedProps } from "./ReactCasualFormTypes";

interface QuestionComboboxProps extends IQuestionExtendedProps {
  inputFieldLabel: string;
  onChangeHandler: (event: any, value: any) => void;
  getOptionLabel: (option: any) => string;
}

const QuestionCombobox: FunctionComponent<QuestionComboboxProps> = ({
  inputFieldLabel,
  onChangeHandler,
  getOptionLabel,
  response,
  setResponse,
  className,
  options,
  isBotTyping,
  setIsBotTyping,
  children,
}) => {
  const domRef = useFocus([response, isBotTyping]);

  return (
    <section className={className}>
      <div>
        <span>{children?.({ setIsBotTyping })}</span>
      </div>

      <div>
        {response ? (
          <Response response={response} setResponse={setResponse} />
        ) : (
          <VisibilityTransition
            isHidden={response === null ? false : !!isBotTyping}
          >
            <Autocomplete
              options={options}
              getOptionLabel={getOptionLabel}
              style={{ width: 300 }}
              onChange={onChangeHandler}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label={inputFieldLabel}
                  variant="outlined"
                  ref={domRef}
                />
              )}
            />
          </VisibilityTransition>
        )}
      </div>
    </section>
  );
};

export default QuestionCombobox;
